<template>
  <div>
    <div class="shopcart">
      <div class="content">
        <div class="content-left" @click="toggleShow">
          <div class="logo-wrapper">
            <div class="logo" :class="{highlight: cartFoodCount}">
              <i class="iconfont icon-shopping_cart" :class="{highlight: cartFoodCount}"></i>
            </div>
            <div class="num" v-show="cartFoodCount">{{cartFoodCount}}</div>
          </div>
          <div class="price" :class="{highlight: cartFoodPrice}">￥{{cartFoodPrice}}</div>
          <div class="desc">另需配送费￥{{info.deliveryPrice}}元</div>
        </div>
        <div class="content-right">
          <div class="pay" :class="payClass">
            {{payText}}
          </div>
        </div>
      </div>

      <div class="shopcart-list" v-show="listShow">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span class="empty">清空</span>
        </div>
        <div class="list-content" ref="list">
          <ul>
            <li class="food" v-for="(food, index) in cartFoods" :key="index">
              <span class="name">{{food.name}}</span>
              <div class="price"><span>￥{{food.price}}</span></div>
              <div class="cartcontrol-wrapper">
                <CartControl :food="food"/>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="list-mask" v-show="listShow" @click="toggleShow"></div>
  </div>
</template>
<script>
  import {mapGetters,mapState} from 'vuex';
  import BScroll from 'better-scroll';
  import CartControl from '../../components/CartControl/CartControl';

  export default {
    data() {
      return {
        isShow: false  // 是否显示购物项列表
      }
    },
    computed: {
      ...mapState(['cartFoods', 'info']),
      ...mapGetters(['cartFoodCount', 'cartFoodPrice']),
      payText() {
        const {cartFoodPrice} = this;
        const {minPrice} = this.info;
        if (cartFoodPrice === 0) {
          return `￥${minPrice}开送`;
        } else if (cartFoodPrice < minPrice) {
          return `￥${minPrice - cartFoodPrice}开始配送`;
        } else {
          return '去结算';
        }
      },
      payClass() {
        const {cartFoodPrice} = this;
        const {minPrice} = this.info;
        if (cartFoodPrice > minPrice) {
          return cartFoodPrice - minPrice >= 0 ? 'enough' : 'not-enough'
        }
      },
      //购物车列表显示
      listShow () {
        // 如果没有数量, 直接隐藏
        if(this.cartFoodCount===0) {
          // 将isShow设置为false
          this.isShow = false;
          return false
        }
        if(this.isShow) {
          this.$nextTick(() => {
            if(!this.scroll) {
              console.log('------')
              this.scroll = new BScroll(this.$refs.list, {
                click: true
              })
            } else {
              //通知scroll刷新(重新进行计算)
              this.scroll.refresh()
            }

          })
        }
        return this.isShow
      }
    },
    methods: {
      toggleShow() {
        //只有数量的时候才切换
        if (this.cartFoodCount) {
          this.isShow = !this.isShow;
        }
      }
    },
    components: {
      CartControl,
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @import "../../common/stylus/mixins.styl"

  .shopcart
    position fixed
    left 0
    bottom 0
    z-index 50
    width 100%
    height 48px
    .content
      display flex
      background #141d27
      font-size 0
      color rgba(255, 255, 255, 0.4)
      .content-left
        flex 1
        .logo-wrapper
          display inline-block
          vertical-align top
          position relative
          top -10px
          margin 0 12px
          padding 6px
          width 56px
          height 56px
          box-sizing border-box
          border-radius 50%
          background #141d27
          .logo
            width 100%
            height 100%
            border-radius 50%
            text-align center
            background #2b343c
            &.highlight
              background $green
            .icon-shopping_cart
              line-height 44px
              font-size 24px
              color #80858a
              &.highlight
                color #fff
          .num
            position absolute
            top 0
            right 0
            width 24px
            height 16px
            line-height 16px
            text-align center
            border-radius 16px
            font-size 9px
            font-weight 700
            color #ffffff
            background rgb(240, 20, 20)
            box-shadow 0 4px 8px 0 rgba(0, 0, 0, 0.4)
        .price
          display inline-block
          vertical-align top
          margin-top 5px
          line-height 24px
          padding-right 12px
          box-sizing border-box
          font-size 16px
          font-weight 700
          color #fff
          &.highlight
            color #fff
        .desc
          display inline-block
          vertical-align bottom
          margin-bottom 15px
          margin-left -45px
          font-size 10px
      .content-right
        flex 0 0 105px
        width 105px
        .pay
          height 48px
          line-height 48px
          text-align center
          font-size 12px
          font-weight 700
          color #fff
          &.not-enough
            background #2b333b
          &.enough
            background #00b43c
            color #fff
    .ball-container
      .ball
        position fixed
        left 32px
        bottom 22px
        z-index 200
        transition all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41)
        .inner
          width 16px
          height 16px
          border-radius 50%
          background $green
          transition all 0.4s linear
    .shopcart-list
      position absolute
      left 0
      top 0
      z-index -1
      width 100%
      transform translateY(-100%)
      &.swipe-enter-active, &.swipe-leave-active
        transition transform .3s
      &.swipe-enter, &.swipe-leave-to
        transform translateY(0)
      .list-header
        height 40px
        line-height 40px
        padding 0 18px
        background #f3f5f7
        border-bottom 1px solid rgba(7, 17, 27, 0.1)
        .title
          float left
          font-size 14px
          color rgb(7, 17, 27)
        .empty
          float right
          font-size 12px
          color rgb(0, 160, 220)

      .list-content
        padding 0 18px
        max-height 217px
        overflow hidden
        background #fff
        .food
          position relative
          padding 12px 0
          box-sizing border-box
          bottom-border-1px(rgba(7, 17, 27, 0.1))
          .name
            line-height 24px
            font-size 14px
            color rgb(7, 17, 27)
          .price
            position absolute
            right 90px
            bottom 12px
            line-height 24px
            font-size 14px
            font-weight 700
            color rgb(240, 20, 20)
          .cartcontrol-wrapper
            position absolute
            right 0
            bottom 6px

  .list-mask
    position fixed
    top 0
    left 0
    width 100%
    height 100%
    z-index 40
    backdrop-filter blur(10px)
    opacity 1
    background rgba(7, 17, 27, 0.6)
    &.fade-enter-active, &.fade-leave-active
      transition all 0.5s
    &.fade-enter, &.fade-leave-to
      opacity 0
      background rgba(7, 17, 27, 0)
</style>



